/* シェアボタン */
.share {
  margin-top: 40px;
  clear: both;
}
.share ul {
  margin: 0;
  padding: 0;
  list-style: none;
}
.share li a {
  display: block;
  text-align: center;
  padding: 10px 5px;
  color: #ffffff;
  font-size: 12px;
  text-decoration: none;
  border-radius: 30px;
}
.share li a:hover {
  opacity: 0.8;
}
.share ul:after {
  content: "";
  display: block;
  clear: both;
}
.share li {
  margin: 20px auto;
  width: 90%;

  list-style: none;
  border-radius: 25px;
  width: 90%;
  -webkit-box-shadow: 0px 5px 30px 0px rgba(0, 0, 0, 0.3);
  box-shadow: 0px 5px 30px 0px rgba(0, 0, 0, 0.3);
}
.share li p {
  display: inline-block;
  padding-top: 10px;
}
.bunsho {
  text-align: center;
  margin: 30px auto;
}
.share-tw {
  height: 40px;
  background-color: #55acee;
}
.share-fb {
  height: 40px;
  background-color: #3b5998;
}
.share-gp {
  height: 40px;
  background-color: #00B900;
}
.share-hb {
  height: 40px;
  background-color: #2c6ebd;
}
.hatenaB:before {
  content: 'B!';
  font-style: normal;
}
@media (max-width: 599px) {
.share span {
    display: inline-block;
    text-indent: -9999px;
}
.share li a {
    padding: 8px 10px;
}
}


